import Navbar from "./Navbar/Navbar";
import { Routes, Route } from "react-router-dom";
import Education from "./Education/Education";
import Skills from "./Skills/Skills";
import Experience from "./Experience/Experience";
import Achievements from "./Achievements/Achievements";
import Projects from "./Projects/Projects";
import AboutMe from "./AboutMe/AboutMe";
import Resume from "./Resume/Resume";

const MainApp = (props)=>{

    return (
      <div className="grid grid-cols-10">
        <div className="col-span-2 h-screen bg-brown-bg">
          <Navbar />
        </div>
        <div className="col-span-8 px-9">
          <Routes>
            <Route index element={<AboutMe />} path="/Portfolio" />
            <Route index element={<Education />} path="/Portfolio/Education" />
            <Route index element={<Skills />} path="/Portfolio/Skills" />
            <Route index element={<Experience />} path="/Portfolio/Experience" />
            <Route index element={<Achievements />} path="/Portfolio/Achievements" />
            <Route index element={<Projects />} path="/Portfolio/Projects" />
            <Route index element={<Resume />} path="/Portfolio/Resume" />
          </Routes>
        </div>
      </div>
    );

}

export default MainApp;